import React from 'react'
import { Typography, Grid, withStyles, Slide, Button } from '@material-ui/core'
import AutoFade from '../components/autoFade'
import Link from 'next/link'
import Head from 'next/head'
import NProgress from 'nprogress'
import Router from 'next/router'
import NameCard from '../components/nameCard'
import Modal from '@material-ui/core/Modal'
import Dialog from '@material-ui/core/Dialog'


// ref:https://github.com/zeit/next.js/blob/canary/examples/with-loading/components/Header.js
Router.onRouteChangeStart = (url) => {
  console.log(`Loading: ${url}`)
  NProgress.start()
}
Router.onRouteChangeComplete = () => NProgress.done()
Router.onRouteChangeError = () => NProgress.done()

const styles = theme => ({
    header: {
        padding: theme.spacing.unit * 2,
        paddingTop: theme.spacing.unit * 4,
        paddingBottom: theme.spacing.unit * 4,
    },

    portfolioTitleHeader: {
        color: theme.palette.primary.main,
        fontFamily: "'Fira Sans', sans-serif",
        [theme.breakpoints.down('xs')]:{
            textAlign: "center",
            paddingBottom: theme.spacing.unit,
        },
    },
    shebang:{
        color: theme.palette.primary.contrastText,
    },
    shebangDark:{
        color: theme.palette.secondary.main,
    },
    a: {
        textDecoration: "none",
        [theme.breakpoints.up('sm')]:{
            display: "inline-block"
        }
    },
    navsItem: {
        margin: "auto 0",
    },
    navItem: {
        marginRight: theme.spacing.unit * 2,
    },
    navsContainer:{
        [theme.breakpoints.down('xs')]:{
            justifyContent: "center",
        },
    },
    titleContainer:{
        [theme.breakpoints.down('xs')]:{
            justifyContent: "center",
        },
    },
});

function Transistion(props){
    return <Slide direction="down" {...props}/>;
}

class Header extends React.Component {

    static defaultProps = {
        dark: false,
        title: "",
    }

    state = {
        open: false,
    };

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };
    

    render() {
        const { classes, dark, title } = this.props;
        const { open } = this.state;
        return (
            <>
            { title && 
            <Head>
                <title key="title">{title}</title> 
            </Head>
            }
            <header className={classes.header}>
                <Grid container spacing={16} justify="center">
                    <Grid item xs={12} sm={4} className={classes.titleContainer}>
                        <Link href="/">
                            <a className={classes.a} >
                                <Typography className={classes.portfolioTitleHeader} variant="display1" align="left">
                                    <span className={dark? classes.shebangDark : classes.shebang} >#!</span> Lincerely
                                </Typography>
                            </a>
                        </Link>
                    </Grid>

                    <Grid item xs={12} sm={6} className={classes.navsItem}>
                        <Grid container spacing={40} justify="flex-end" className={classes.navsContainer}>
                            <Grid className={classes.navItem}>
                                <Link href="/bio">
                                    <Button className={classes.a} color={dark ? "secondary" : "default"} >
                                        bio
                                    </Button>
                                </Link>
                            </Grid>
                            <Grid className={classes.navItem}>
                                <Link href="/works">
                                    <Button className={classes.a} color={dark ? "secondary" : "default"} >
                                        works
                                    </Button>
                                </Link>
                            </Grid>
                            <Grid className={classes.navItem}>
                                    <Button  variant="outlined" onClick={this.handleOpen} className={classes.a} color={dark ? "secondary" : "default"} >
                                        contact
                                    </Button>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </header>
            
                <Modal
                    aria-labelledby="name-card-modal"
                    aria-describedby="hidden name card shown when click contact button"
                    open={this.state.open}
                    onClose={this.handleClose}
                    TransitionComponent={Transistion}
                    className={classes.modal}
                >
                    <Slide in={this.state.open} direction="down">
                    <NameCard className={classes.nameCard} onClickHandler={this.handleClose}/>
                    </Slide>
                </Modal>

            </>
        )
    }
}

export default withStyles(styles)(Header);




