/* eslint-disable jsx-a11y/anchor-is-valid */

import React from 'react';
import PropTypes from 'prop-types';
import {Button, Grid, CircularProgress} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles'
import Header from '../components/header'
import Footer from '../components/footer'
import BackToTopIcon from '@material-ui/icons/KeyboardArrowUp'
import ScrollToTop from 'react-scroll-up'
import Dynamic from 'next/dynamic'

/**
 * Works
 *
 * Load data from json and print list of works
 */


const styles = theme => ({
	root:{
        padding: `0 ${theme.spacing.unit * 3}px`,
    },
});

const CardGridList = Dynamic(import('../components/cardGridList'),
    {
        loading:() => <CircularProgress style={{margin: "16px"}} />,
    }
)

class Works extends React.Component {

	static async getInitialProps() {
		const data = await import('../static/portfolio-data/data.json')
		return { data: data }
	}

	render(){
		const { classes, data } = this.props;
		return (
			<>
			<div>
				<Header title="Lincerely | Works"/>
					<Grid container justify="center"  className={classes.root}>
						<Grid item xs={12} sm={10}>
							<CardGridList data={data}/>
						</Grid>
					</Grid>

					
				<Footer/>
			</div>

			<ScrollToTop showUnder={160}>
				<Button variant="fab" color="secondary" aria-label="Back To Top" className={classes.fab}>
					<BackToTopIcon />
				</Button>
			</ScrollToTop>
			</>
		);
	}
}

Works.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Works);



