import {Grid, Typography, withStyles} from '@material-ui/core';
import React from 'react';
import Link from 'next/link';
import LoveIcon from '@material-ui/icons/Favorite'

/**
 * Footer
 * 
 * Consist of links to main parts of the portfolio and a copyright statement
 */

const styles = theme => ({
    footer:{
        paddingTop: theme.spacing.unit*2,
        paddingBottom: theme.spacing.unit*2,
        minHeight: "5vh",
    },
    redIcon:{
        color: "red",
    },
    a:{
        color: theme.palette.primary.dark,
        textDecoration:"none"
    },
    aDark:{
        color: theme.palette.secondary.dark,
        textDecoration:"none"
    }
});

class Footer extends React.Component{

    static defaultProps = {
        dark: false,
    }

    RedLoveIcon = () => {
        return <LoveIcon className={this.props.classes.redIcon} fontSize="inherit"/> 
    }

    render () {
        const {classes, dark} = this.props;
        return (
    <footer className={classes.footer}>
        <Grid container spacing={0} justify="center">
            <Grid item xs={12} sm={8} className={classes.grid}>
                <Typography variant="caption" align="center" color={dark ? "secondary" : "default"}>
                    Made with{' '}
                    {dark? <LoveIcon fontSize="inherit"/> : <this.RedLoveIcon/>}
                    {' '}by Lincoln. Powered with{' '}
                    <a href="https://nextjs.org/" className={dark? classes.aDark : classes.a} target="_blank">
                        Next.js
                    </a>
                </Typography>
            </Grid>
        </Grid>
    </footer>
        )
    }
}


export default withStyles(styles)(Footer);




