import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import SvgIcon from '@material-ui/core/SvgIcon';
import MailIcon from '@material-ui/icons/Mail'
import Obfuscate from 'react-obfuscate';
import Tilt from 'react-tilt'
import { ClickAwayListener } from '../node_modules/@material-ui/core';

const styles = theme => ({
  card:{
    width:"480px",
    height:"240px",
    backgroundColor: theme.palette.secondary.dark,
    backgroundImage: ["linear-gradient(#61616155 2px, transparent 2px)",
                      "linear-gradient(90deg, #61616125 2px, transparent 2px)",
                      "linear-gradient(#61616115 1px, transparent 1px)",
                      "linear-gradient(90deg, #61616115 1px, transparent 1px)"],
    backgroundSize: "100px 100px, 100px 100px, 20px 20px, 20px 20px",
    backgroundPosition: "-2px -2px, -2px -2px, -1px -1px, -1px -1px",
    [theme.breakpoints.down('xs')]:{
      transform: "rotate(90deg)",
    }
  },
  names:{
    marginBottom:theme.spacing.unit *2,
    marginLeft: -theme.spacing.unit *6,
    marginRight: -theme.spacing.unit *10,
    padding:theme.spacing.unit*2,
    paddingLeft:theme.spacing.unit*6,
    backgroundColor: theme.palette.primary.main,
    color:"white",

  },
  root:{
    outline: 0,
    display:"flex",
    justifyContent:"center",
    alignItems:"center",
    height:"100%",
  }
});

function MediaControlCard(props) {
  const { classes, onClickHandler } = props;

  return (
    <div className={classes.root}>
    <ClickAwayListener onClickAway={onClickHandler}>
      <Tilt className="Tilt" options={{ max : 25 }} style={{ height: 240, width: 480}}>
        <Card elevation={4} className={[classes.card,"Tilt-inner"]}>
          <CardContent>
            <div style={{width:"50%", height:"100%"}}>
              <div className={classes.names}>
                <Typography variant="caption" color="textSecondary" >Software Developer</Typography>
                <Typography variant="headline" gutterBottom color="inherit">LI Ling Hin Lincoln</Typography>
                <div className={classes.icons}>
                  <IconButton href="https://github.com/lincerely" target="_blanks" aria-label="github link">
                      <SvgIcon>
                          <svg aria-labelledby="simpleicons-github-icon" role="img" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><title id="simpleicons-github-icon">GitHub icon</title><path d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"/></svg>
                      </SvgIcon>
                  </IconButton>
                  <Obfuscate email="lincerely@gmail.com"><IconButton><MailIcon/></IconButton></Obfuscate>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </Tilt>
      </ClickAwayListener>
    </div>
  );
}

export default withStyles(styles)(MediaControlCard);


