const techKeys = Object.keys(techs)

class Bio extends React.Component {

    render(){
        const {classes} = this.props;

        return(
            <div>
                <Header title="Lincerely | Bio"/>

                <Grid container justify="center" alignItems="center" className={classes.root}>
                    <Grid item xs={12} sm={10}>
                        
                    <Masonry>
                        {/* Experience */}
                        <div className={classes.cardContainer}>
                            <Card className={classes.card} elevation={0}>
                                <CardHeader title="Experience" />
                                <Stepper orientation="vertical"nonLinear>
                                {
                                    experiences.map((exp,index) => (
                                        <Step key={"exp".concat(index)} active>
                                        <StepLabel icon={exp.icon}> {exp.year} </StepLabel>
                                            <StepContent>
                                                <Typography variant="caption"> {exp.place} </Typography> 
                                                <Typography variant="body1"> {exp.detail} </Typography>
                                            </StepContent>
                                        </Step>
                                    ))
                                }
                                </Stepper>
                            </Card>
                        </div>
                       
                       {/* Awards */}
                        <div className={classes.cardContainer}>
                            <Card className={classes.card} elevation={0}>
                                <CardHeader title="Awards" />
                                <Stepper orientation="vertical"nonLinear>
                                {
                                    awards.map((award,index) => (
                                        <Step key={"award".concat(index)} active>
                                        <StepLabel icon={award.icon} > {award.year} </StepLabel>
                                            <StepContent >
                                                <Typography variant="caption"> {award.place} </Typography> 
                                                <Typography variant="body1"> {award.detail} </Typography>
                                                {award.other}
                                            </StepContent>
                                        </Step>
                                    ))
                                }
                                </Stepper>
                            </Card>
                        </div>

                        {/* Tech Skills */}
                        <div className={classes.cardContainer}>
                            <Card className={classes.card} elevation={0}>
                                <CardHeader title="Technical Skills"/>
                                <CardContent>
                                    {
                                        techKeys.map((key, index) => (
                                            <div key={key} className={classes.gutterBottom}>
                                            <Typography  variant="subheading"  gutterBottom>{key}</Typography>
                                            {
                                                techs[key].map( (value, innerIndex) => (
                                                <Chip label={value} key={key.concat(innerIndex)} className={classes.chip} />
                                            ))}
                                            </div>
                                        ))
                                    }
                                </CardContent>
                            </Card>
                        </div>
                        
                        {/* Interests */}
                        <div className={classes.cardContainer}>
                            <Card className={classes.card} elevation={0}>
                                <CardHeader title="Interests"/>

                                <CardContent>
                                    <List>
                                        {
                                            interests.map((interest, index) => (
                                                <ListItem key={"interest".concat(index)}>
                                                    <ListItemIcon>
                                                        {interest.icon}
                                                    </ListItemIcon>
                                                    <ListItemText primary={interest.title} secondary={interest.detail}/>
                                                </ListItem>
                                            ))
                                        }
                                    </List>
                                </CardContent>
                            </Card>
                        </div>

                        {/* Lincerely */}
                        <div className={classes.cardContainer}>
                            <Card className={classes.card} elevation={0}>
                                <CardHeader title="Lincerely?"/>

                                <CardContent>
                                    <Typography className={classes.detail}>
                                        Lin + celery + sincerely (don't ask me the reason why, idk)
                                    </Typography>
                                </CardContent>
                            </Card>
                        </div>
                        </Masonry>
                        </Grid>
                    </Grid>
                <Footer/>
            </div>
        )
    }
}

export default withStyles(styles)(Bio);


