import React from 'react'
import {Fade} from '@material-ui/core'

class AutoFade extends React.Component {
    state = {
      checked: false,
    };

    static defaultProps = {
        timeout: 1200,
    }

    componentDidMount(){
        setTimeout(() => {this.setState(state => ({ checked: !state.checked }))},this.props.timeout);
    }
  
    render() {
      const { classes, children } = this.props;
      const { checked } = this.state;
  
      return (
        <div>
          <Fade in={checked}>
            {children}
          </Fade>
        </div>
      );
    }
}

export default AutoFade;


